import React from 'react';
import get from 'lodash/get';
import { useFormik } from 'formik';
import WarehouseIcon from '@mui/icons-material/Warehouse';

import TimeFrameForm from './timeFrame';
import { SearchField, TextField } from '../components';

import { FacilitiesQuery } from '../api';
import { Facility, Freight, TimeFrameType } from '../utils/dataTypes';
import FreightForm from './freight';

export default function StopPickUpForm({
  fieldName,
  disabled,
  formik,
}: {
  readonly fieldName: string;
  readonly disabled?: boolean;
  readonly formik: ReturnType<typeof useFormik<any>>;
}) {
  const formikValue = get(formik.values, fieldName);

  return (
    <>
      <SearchField<Facility>
        fieldName={`${fieldName}.facility`}
        label="Facility"
        getSelectedLabel={(selectedItem) => `${selectedItem.name}`}
        getItemLabel={(item) => `${item.name}`}
        getItemSecondLabel={(item) => `${item.address}`}
        ListIcon={WarehouseIcon}
        queryFn={FacilitiesQuery}
        startEmpty={false}
        orderby="name"
        order="asc"
        disabled={disabled}
        formik={formik}
      />
      <TimeFrameForm
        fieldName={`${fieldName}.timeFrame`}
        values={[TimeFrameType.FCFS, TimeFrameType.APPT, TimeFrameType.ASAP]}
        disabled={disabled}
        formik={formik}
      />
      {formikValue.freightList.map((freight: Freight, index: number) => (
        <FreightForm
          key={index}
          fieldName={`${fieldName}.freightList[${index}]`}
          disabled={disabled}
          formik={formik}
        />
      ))}
      <TextField
        fieldName={`${fieldName}.addInfo`}
        label="Additional stop info"
        disabled={disabled}
        formik={formik}
      />
    </>
  );
}
